.section{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1.5em;
}

.section span{
    width: 16em;
}
.pdf-control{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.document{
    z-index: 1;
    display: flex;
    justify-content: center;
    margin: 1em 0 1em;
    box-shadow: 0em 0em 1em var(--shadow1);
    min-width: 58em;
    min-height: 75em;
    background-color: white;
}

.document p{
    margin: 1em;
}

.download{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.download button{
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: none;
}

.download a{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: white;
    color: var(--text);
    border: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 1em;
    padding: .5em;
    width: 11em;
    margin: 0 0 0 .5em ;
    box-shadow: 0.2em 0.2em .5em var(--shadow1);
}

.download a:hover{
   cursor: pointer;
   color: var(--hoverText);
}

.download a:active{
   background-color: var(--active);
}

.download button, a:focus{
   outline: none;
}

.unavailable{
    color: lightgray !important;
    cursor: not-allowed !important;
    background-color: white !important;
    pointer-events: none;
}


.icon{
    margin-left: .5em;
}

.main-icon{
    font-size: 1.5em;
}

a:link{
    text-decoration: none;
}

.page-controls{
    z-index: 2;
    position: absolute;
    width: inherit;
    left: 50%;
    opacity: 0.3;
    transform: translateX(-50%);
    transition: opacity .25s ease-in-out;
    -moz-transition: opacity .25s ease-in-out;
    -webkit-transition: opacity .25s ease-in-out;
    background-color: white;
}
.page-controls:hover {
    opacity: 1;
    box-shadow: 0 0 .7em var(--active);
}

.up{
    top: 5%;
}
.down{
    bottom: 2%;
}

.page-controls span {
    padding: .5em 1em .5em 1em;
}

.page-controls button{
    border: none;
    padding: .5em 1em .5em 1em;
    background-color: white;
    cursor: pointer;
}

.page-controls button:focus{
    border: none;
    outline: none;
    background-color: white;
}

.page-controls button:hover{
    color: var(--text);
    background-color: var(--active);
}

.inactive{
    opacity: 0.2;
    background-color: white !important;
    color:#424242 !important;
}