.home{
    position: relative;
    height: calc(100vh - 3em);
}

.back{
    position: absolute;
    min-width:100%;
    max-width: 200%;
    height: auto;
    min-height: 100%;
    display: block;
    bottom: 0;
    overflow-y: hidden;
    z-index: 1;
}

.over-purple{
    z-index: 2;
    position: absolute;
    background-color:var(--darkText);
    width:100vw;
    height: 100vh;
    display: block;
    bottom: 0;
    opacity: .6;
    overflow-y: hidden;
}

.hello{
    z-index: 3;
    position: absolute;
    bottom: 25vh;
    left: 10vw;
    font-size: 6vw;
    color: white;
    text-shadow: 0 0 0.2rem  black;
}

.iam{
    color: white;
    position: relative;
    display: flex;
    flex-flow: row;
    justify-content: left;
    align-items: center;
    top: -.5em;
}

.keywords{
    padding-left: .2em;
    color: white;
    font-size: 6vw;
    font-weight: bold;
}

.description{
    z-index: 4;
    color: white;
    position: absolute;
    background-color: #660166;
    opacity: .8;
    width: 100%;
    padding: 1.5em 3em .8em 3em;
    bottom: 0;
    text-align:center;
}

.tab{
    font-size: 10vw;
}